import React, { useState, useEffect } from 'react';
import ButtonPanel from './ButtonPanel/ButtonPanel';
import InputFilter from './InputFilter/InputFilter';
import Table from './Table/Table';
import s from './App.module.css';

const App = ({ data }) => {

    const [height, setHeight] = useState(window.innerHeight);

    useEffect(() => {
        window.addEventListener('resize', () => {
            setHeight(prevHeight => {
                if (Math.abs(prevHeight - window.innerHeight) > 10) {
                    return window.innerHeight
                }
                return prevHeight;
            })
        })
    }, []);


    const [sortBy, setSortBy] = useState("id");
    const [order, setOrder] = useState(false);

    const handleSort = (item) => {

        setSortBy(prevItem => {

            if (prevItem === item) {
                setOrder(prevOrder => !prevOrder);
            } else {
                setOrder(false)
            }
            return item;
        });
    }

    const [filter, setFilter] = useState("");
    const handleFilter = event => {
        setFilter(event.target.value);
    }

    const [page, setPage] = useState(1);
    const handlePagination = page => {
        setPage(prevPage => {
            return page;
        })
    }


    // Сортировка по столбцу
    const sortedData = [...data].sort((item1, item2) => {
        switch (item1[sortBy] > item2[sortBy]) {
            case true:
                return order ? -1 : 1
            case false:
                return order ? 1 : -1
            default:
                return 0
        }
    })

    //Фильтрация по значению инпута
    const filteredData = sortedData.filter(item => {
        return Object.values(item)
            .join()
            .toLowerCase()
            .includes(filter.toLowerCase())
    })

    // Примерная высота header+input
    const headerHeight = height * 0.06;

    // Примерная высота кнопок пагинации
    const buttonsPanelHeight = height * 0.05

    // Примерная высота tableHead
    const tableHeadHeight = height * 0.08

    // Свободное пространство в tbody
    const freeSpace = height - headerHeight - buttonsPanelHeight - tableHeadHeight;

    // Приблизительная высота table row
    const randomRowHeight = 30;
    const maxPages = Math.floor(freeSpace / randomRowHeight);


    const rowsPerPage = maxPages <= 50 ? maxPages : 50;
    const rowHeight = (freeSpace - 2 * (rowsPerPage + 1)) / rowsPerPage;


    // Отображаемые на текущей странице записи
    const visibleData = filteredData.slice((page - 1) * rowsPerPage, page * rowsPerPage);

    const totalPages = Math.ceil(filteredData.length / rowsPerPage);


    return (
        <div className={s.container}>
            <header className={s.header}>
                <InputFilter value={filter} handleChange={handleFilter} />
            </header>
            <Table
                head={Object.keys(data[0])}
                handleClick={handleSort}
                currentSort={sortBy}
                order={order}
                data={visibleData}
                rowHeight={rowHeight}
            />
            <ButtonPanel
                currentPage={page}
                totalPages={totalPages}
                handleClick={handlePagination}
            />

        </div>
    )
}

export default App;