import React from 'react';
import { CaretDownOutlined, CaretUpOutlined } from '@ant-design/icons';
import s from './TableHead.module.css';

const TableHead = (props) => {

    const { columns, handleClick, currentSort, order } = props;

    const tableHeadWidth = `${100 / columns.length}%`

    return (
        <thead>
            <tr className={s.tableHeadRow}>
                {columns.map((item, i) => {
                    return <th
                        className={s.tableHeadCell}
                        style={{ width: tableHeadWidth }}
                        key={i}
                        onClick={() => {
                            handleClick(item);
                        }}
                    >
                        {item} <span>
                            {currentSort === item ?
                                order ? <CaretUpOutlined /> : <CaretDownOutlined />
                                : ""
                            }
                        </span>
                    </th>
                })}
            </tr>
        </thead>
    )
}

export default TableHead