import React from 'react';
import TableRow from './TableRow';


const TableBody = (props) => {
    const {
        content,
        rowHeight
    } = props;

    return (
        <tbody>
            {
                content.map(item => <TableRow
                    key={item.id}
                    data={item}
                    rowHeight={rowHeight}
                />)
            }
        </tbody>
    )
}

export default TableBody