import React from 'react';
import s from './ButtonPanel.module.css';



const createPaginationButtons = (page, step, maxPage) => {
    const result = [];
    for (let i = page - step; i <= page + step; i++) {
        if (i > 0 && i <= maxPage) {
            result.push(i);
        }
    }
    return result;
}



const ButtonPanel = (props) => {

    const { currentPage, totalPages, handleClick } = props;

    const getPaginationButtons = () => {
        return (
            createPaginationButtons(currentPage, 2, totalPages).map(item => {

                let paginationButtonClasses = (currentPage === item) ? `${s.paginationButton}` : `${s.paginationButton} ${s.activePage}`;

                return <button
                    key={item}
                    className={paginationButtonClasses}
                    onClick={() => handleClick(item)}
                >
                    {item}
                </button>
            })
        )

    }


    return (
        <div className={s.paginationPanel}>
            {getPaginationButtons()}
        </div>
    )
}

export default ButtonPanel;